async function GetProgresSetupStatusDetail(instance, modulename) {
    var vm = instance;
    let SetupProgress = 0;
    let params = {};
    params["moduleName"] = modulename;
    if (modulename == 'Asset') {
        await DataService.GetProgresSetupStatusDetail(params)
            .then(function (response) {
                if (response.data.result.length > 0) {
                    response.data.result.forEach(element => {
                        if (element.PROGRESS != null) {
                            SetupProgress = SetupProgress + element.PROGRESS;
                        }
                    });
                     ClickSection[0].SetupProgress = SetupProgress;   
                    // if (SetupProgress > 0 && SetupProgress <= 25) {
                    //     ClickSection[0].progressColor = 'progress-bar progress-bar-striped';
                    // }                 
                    // else if (SetupProgress > 0 && SetupProgress <= 50) {
                    //     ClickSection[0].progressColor = 'progress-bar progress-bar-striped';
                    // }
                    // else if(SetupProgress > 0 && vm.SetupProgress<=75){
                    //     ClickSection[0].progressColor ="progress-bar progress-bar-striped"
                    // }
                    // else if(SetupProgress > 0 && SetupProgress < 100) {
                    //     ClickSection[0].progressColor = 'progress-bar progress-bar-striped';
                    // }
                    // else if(SetupProgress==100) {
                    //     ClickSection[0].progressColor = 'green p100';
                    // }
                    // else if(SetupProgress == 0) {
                    //     ClickSection[0].progressColor = 'progress-bar progress-bar-striped';
                    // }

                    if(SetupProgress==100) {
                            ClickSection[0].progressColor = 'progress-bar progress-bar-striped green p100';
                        }
                }
            });
    }
}
export default { leftSearchFields, ClickSection, GetProgresSetupStatusDetail };