import TagNamePopup from '../Modal/TagNamePopup.vue'
import SearchSechma from "../../views/AdvancedSetup/SearchSchema"

export default {
    props: {
        popupCondition: {
            type: Boolean,
            required: true,
        },
        AseetTypeIde: {
            type: Number,
            required: false
        },
        FromPage: {
            type: String,
            required: false
        }
    },
    components: {
        TagNamePopup
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [{
                layoutType: "triple",
                group_name: 'Product Type',
                group_display_name: this.$t('ItemType'),
                Data: [

                    {
                        astype: "TextField",
