                        name: "ItemNameType",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Status'),
                        name: "Status",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                        },
                    },
                    {
                        astype: "SlotField",
                        label: this.$t('ProductTypeCode'),
                        name: "ProductTypeCode",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            max: 4
                        }
                    },
                    {
                        astype: "toggleField",
                        label: this.$t('Container'),
                        name: "Container",
                        value: "",
                        placeholder: "",
                        config: {
                            onChange: this.ContainerDeleteConfirm
                        }
                    },
                    {
                        astype: "SlotField",
                        showLabel: false,
                        label: this.$t('Container'),
                        name: "tblContainer",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [],

                        },
                        visibility: true,
                    },
                ],
            },
            {
                layoutType: "triple",
                Data: [
                    {
                        astype: "FileField",
                        showLabel: true,
                        label: this.$t('AttachItemImage'),
                        name: "AttachItemImage",
                        value: "",
                        placeholder: "",
                        validationRules: "",
                        FieldIndex: 0,
                        config: {

                            maxSize: 5,
                            // accept: [{
                            //     fileType: 'png',
                            //     iconClass: 'fa-file-image-o'
                            // },
                            // {
                            //     fileType: 'jpg',
                            //     iconClass: 'fa-file-image-o'
                            // },
                            // {
                            //     fileType: 'jpeg',
                            //     iconClass: 'fa-file-image-o'
                            // },
                            //     // {
                            //     //     fileType: 'pdf',
                            //     //     iconClass: 'fa-file-archive-o'
                            //     // }
                            // ],
                            accept: [],

                            showSelectedFiles: [{
                                attachmentId: "1",
                                attachmentName: "",
                                attachmentPath: "",
                            },],
                            onClickSelectedFiles: this.onClickSelectedFile,
                            onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                            onChange: this.setFile1,
                            showAddIcon: false,
                            onAddButtonClick: this.AddFileMore,
                            multiple: false
                        },
                    },],
            },
            {
                layoutType: "Single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Description'),
                    name: "Description",
                    value: "",
                    placeholder: "",
                },]
            },
            {
                layoutType: "four",
                group_name: 'Product Categories',
                group_display_name: this.$t('ProductCategories'),
                Data: [{
                    astype: "toggleField",
                    label: this.$t('Asset_No'),
                    name: "Asset_No",
                    value: "",
                    placeholder: "",
                },
                {
                    astype: "toggleField",
                    label: this.$t('Consumable'),
                    name: "Consumable",
                    value: "",
                    placeholder: "",
                },
                {
                    astype: "toggleField",
                    label: this.$t('License'),
                    name: "License",
                    value: "",
                    placeholder: "",
                },
                {
                    astype: "toggleField",
                    label: this.$t('MaterialSpareParts'),
                    name: "MaterialSpareParts",
                    value: "",
                    placeholder: "",
                },
                ]
            },
            {
                layoutType: "single",
                Data: [{
                    astype: "MultiSelectField",
                    label: this.$t('Tags'),
                    name: "Tags",
                    value: "",
                    mode: "tag",
                    placeholder: this.$t('Select') +" "+this.$t('Tags'),
                    config: {
                        options: [],
                        showAddIcon: true,
                        onAddButtonClick: this.OpenAddTagPopup
                    },
                },]
            },

            {
                layoutType: "four",
                group_name: 'Hardware Integration Type',
                group_display_name: this.$t('HardwareIntegrationType'),
                Data: [{
                    astype: "toggleField",
                    label: this.$t('Time_n_Attendance'),
                    name: "Time_n_Attendance",
                    value: "",
                    placeholder: "",
                },
                {
                    astype: "toggleField",
                    label: this.$t('Camera'),
                    name: "Camera",
